	/* marker跳动的动画 */
.markerBounce {
		-webkit-animation: bounce 0.5s infinite ease-in-out alternate;
		        animation: bounce 0.5s infinite ease-in-out alternate;
}
	/* marker飞入的动画 */
.markerFlash {
		-webkit-animation: flash 0.5s ease-in 1 normal forwards;
		        animation: flash 0.5s ease-in 1 normal forwards;
}
	/* 跳动的动画 */
@-webkit-keyframes bounce {
0% {
			-webkit-transform: translate(0, 0);
			        transform: translate(0, 0);
}
100% {
			-webkit-transform: translate(0, -0.3125rem);
			        transform: translate(0, -0.3125rem);
}
}
@keyframes bounce {
0% {
			-webkit-transform: translate(0, 0);
			        transform: translate(0, 0);
}
100% {
			-webkit-transform: translate(0, -0.3125rem);
			        transform: translate(0, -0.3125rem);
}
}
	/* 飞入的动画 */
@-webkit-keyframes flash {
0% {
			-webkit-transform: translate(0, -6.25rem);
			        transform: translate(0, -6.25rem);
}
100% {
			-webkit-transform: translate(0, 0);
			        transform: translate(0, 0);
}
}
@keyframes flash {
0% {
			-webkit-transform: translate(0, -6.25rem);
			        transform: translate(0, -6.25rem);
}
100% {
			-webkit-transform: translate(0, 0);
			        transform: translate(0, 0);
}
}
